export default {
  data() {
    return {
      searchQuery: '',
      posts: []
    }
  },
  watch: {
    async searchQuery(searchQuery) {
      if (!searchQuery) {
        this.posts = []
        return
      }
      this.posts = await this.$content("blog")
        .limit(6)
        .search(searchQuery)
        .fetch()
    }
  }
}
